.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 20%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    display: block;
    overflow-y: auto;
    height: auto;
}

@media (min-width: 600px) {
    .Modal {
        width: 50%;
        left: 25%;
    }
}